import mTabs from '@/components/ms-tabs/ms-tabs';
// import sTabs from '@/components/s-tabs';
// import sTab from '@/components/s-tab';
import uniPopup from "@/components/uni-popup/uni-popup.vue";
import uniTag from "@/components/uni-tag/uni-tag.vue";
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue';
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue';
import {
	ActionXzbx
} from '@/utils/actions.js';
import {
	FmtReimburseColorStatus
} from '@/common/spb.js';
import {
	reimburseQueryReimburses,
	dspReimburse,
	yspReimburse,
	csMeReimburse,
	meDoReimburse
} from '@/service/service.js';
import store from '@/store/index.js';

export default {
	components: {
		uniSwipeAction,
		uniSwipeActionItem,
		uniPopup,
		// sTabs,
		// sTab,
		mTabs
	},
	data() {
		let deftListData = {
			amount: 0.0,
			invoiceQuantity: 0,
			allReimburseList: []
		};
		return {
			list2: [{
				title: '待审批'
			}, {
				title: '已审批'
			}, {
				title: '抄送我'
			}, {
				title: '已发起'
			}],
			active: 0,
			sp: [],
			csMoney: 0,
			filter: {
				invoiceStartTime: "",
				invoiceEndTime: ""
			},
			reimburseStatuses: [{
					value: 'Auditing',
					label: '待审批',
					ttype: 'default'
				},
				{
					value: 'Rejected',
					label: '已驳回',
					ttype: 'default'
				},
				{
					value: 'WaitingTickets',
					label: '待收票',
					ttype: 'default'
				},
				{
					value: 'WaitingPayment',
					label: '待付款',
					ttype: 'default'
				},
				{
					value: 'Paid',
					label: '已付款',
					ttype: 'default'
				}
			],
			listData: deftListData,
			swipeOptions: [{
				text: '删除',
				style: {
					backgroundColor: '#DD524D'
				}
			}]
		};
	},
	filters: {
		formatDate: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return y + '.' + MM + '.' + d;
		}
	},
	watch: {
		active() {
			let that = this;
			that.csMoney = 0;
			if(this.active == 0) {
				that.dspReimburse();
			} else if(this.active == 1) {
				that.yspReimburse();
			}else if(this.active == 2) {
				that.csMeReimburse()
			}else if(this.active == 3) {
				that.meDoReimburse();
			}
		}
	},
	computed: {

	},
	onLoad() {
		if (store.state.reimburse.audit) {
			this.reimburseStatuses.forEach((item) => {
				if (item.value == store.state.reimburse.audit) {
					item.slted = true;
					item.ttype = 'primary';
				} else {
					item.slted = false;
					item.ttype = 'default';
				}
			});
			store.state.reimburse.audit = '';
		}
	},
	// 下拉刷新列表
	onPullDownRefresh() {
		console.log('下拉刷新报销列表..');
		this.queryReimburses(function() {
			uni.stopPullDownRefresh();
		});
	},
	methods: {
		dspReimburse(){
			let that = this;
			let userId = store.state.userInfo.userId;
			dspReimburse({
				filter: {
					source: "MP-FEISHU",
					userId: userId
				},
				aging: {
					total: 1,
					size: 10,
					current: 1
				}
			}).then((dspRes) => {
				console.log(1111, dspRes)
				if (dspRes.success) {
					that.sp = dspRes.data.records;
					that.sp.map((ev) => {
						console.log(ev)
						that.csMoney += ev.reimburseAmount
					})
				}
			});
		},
		yspReimburse(){
			let that = this;
			let userId = store.state.userInfo.userId;
			yspReimburse({
				filter: {
					source: "MP-FEISHU",
					userId: userId
				},
				aging: {
					total: 1,
					size: 10,
					current: 1
				}
			}).then((yspRes) => {
				console.log(1111, yspRes)
				if (yspRes.success) {
					that.sp = yspRes.data.records;
					that.sp.map((ev) => {
						console.log(ev)
						that.csMoney += ev.reimburseAmount
					})
				}
			});
		},
		csMeReimburse(){
			let that = this;
			let userId = store.state.userInfo.userId;
			csMeReimburse({
				filter: {
					source: "MP-FEISHU",
					userId: userId
				},
				aging: {
					total: 1,
					size: 10,
					current: 1
				}
			}).then((cspRes) => {
				console.log(1111, cspRes)
				if (cspRes.success) {
					that.sp = cspRes.data.records;
					that.sp.map((ev) => {
						console.log(ev)
						that.csMoney += ev.reimburseAmount
					})
				}
			});
		},
		meDoReimburse(){
			let that = this;
			let userId = store.state.userInfo.userId;
			meDoReimburse({
				filter: {
					source: "MP-FEISHU",
					userId: userId
				},
				aging: {
					total: 1,
					size: 10,
					current: 1
				}
			}).then((mspRes) => {
				console.log(1111, mspRes)
				if (mspRes.success) {
					that.sp = mspRes.data.records;
					that.sp.map((ev) => {
						that.csMoney += ev.reimburseAmount
					})
				}
			});
		},
		handleStartTime(event) {
			this.filter.invoiceStartTime = event.detail.value;
			console.log("开始时间：" + this.filter.invoiceStartTime)
		},
		handleEndTime(event) {
			this.filter.invoiceEndTime = event.detail.value;
			console.log("结束时间：" + this.filter.invoiceEndTime)
		},
		// 弹出筛选窗口
		handleChooseFilter() {
			this.$refs.filter.open();
		},
		// 筛选条件显隐
		handleFilterPopupChange(e) {
			let args = {
				animation: true
			};
			if (e.show) {
				uni.hideTabBar(args);
			} else {
				uni.showTabBar(args);
			}
		},
		// 重置过滤条件
		handleFilterReset() {
			console.log('重置过滤条件..');
			this.filter = {
				invoiceStartTime: "",
				invoiceEndTime: ""
			};
			this.reimburseStatuses.forEach((item, idx) => {
				if (item.slted) {
					this.handleFilterReimburseStatusTag(idx);
				}
			});
		},
		// 过滤报销状态
		handleFilterReimburseStatusTag(idx) {
			console.log('选择过滤报销状态标签..', idx);
			let itype = this.reimburseStatuses[idx];
			itype.slted = !itype.slted;
			itype.ttype = itype.slted ? 'primary' : 'default';
		},
		// 确认过滤查询
		handleFilterQuery() {
			console.log('确认过滤查询..');
			// this.queryReimburses();
			this.active();
			this.$refs.filter.close();
		},
		// 处理发票热区
		handleItemHotspot(id) {
			// console.log('处理子项热区点击事件..', e);
			// let ids = id;
			uni.navigateTo({
				url: `../../reimburse/detail/detail_tt?biz=ck&id=` + id
			});
		},
		// 处理新增报销
		handleXzbxAction() {
			ActionXzbx();
		},
		// 格式化报销状态
		fmtReimburseStatus(istatusColor) {
			const colorStatus = FmtReimburseColorStatus(istatusColor);
			return "<text style='color:" + colorStatus.color + "'>" + colorStatus.label + "</text>";
		},
		fmtMoney(money) {
			if (!money) return 0.00;
			return parseFloat(money).toFixed(2);
		}
	}
}
